input[type=radio] {
  position: absolute;
  visibility: hidden;
  display: none;  
}

label {
  color: #D7BDE2;
  display: inline-block;
  cursor: pointer;
  font-weight: bold;
  text-align: center;
  padding: 5px 35px;
  
  background-color: #515A5A ;
}
label:hover{
  background-color: #292829;
}
input[type=radio]:checked + label{
  color:  #D7BDE2;
  background: black;
}

label + input[type=radio] + label {
  border-left: solid $borderWidth $fg;
}
.radio-group {
  border: solid $borderWidth $fg;
  display: inline-block;
  margin: 0px;
  border-radius: 10px;
  height: 30px;
  overflow: hidden;
}

